$base-background: #242424;
$link-color: white;
$brick-color: #B0623F;
$patchwork-color: #878C54;
$margin-row: 5px;

* {
    box-sizing: border-box;
}

html,body {
    margin: 0;
    background: $base-background;
    font-family: 'Roboto', sans-serif;
}

p,h1,h2,h3 {
    margin-left: $margin-row;
}

a {
    text-decoration: none;
    color: $link-color;
}

a.current {
    color: gray;
}

h1, h2 {
    margin: 0;
    padding: 0 $margin-row;
    color: white;
    background: black;
}

h1 {
    font-size: 20px;
    font-weight: bold;
}

h2 {
    font-size: 15px;
    font-weight: normal;
}

#topbar {
    color: white;
    background: black;
    display: flex;
    align-items: center;
    padding: 0 $margin-row;
}

#topbar #brand {
    font-size: 15px;
}

#topbar nav {
    flex: 1;
    background: black;
    font-weight: normal;
    font-size: 15px;
    text-align: right;
    padding: 3px 0;    
}

#topbar nav a {
    padding: 3px 3px 3px 5px;
    text-decoration: none;
}    

.logo {
    font-family: 'Days One', sans serif;
    font-weight: normal;
}

#topbar .subtitle {
    font-size: 15px;
    text-align: left;
    padding: 0 4px;
}

#splash-container {
    width: 100%;
    overflow: hidden;
    margin: 0;
    background: black;
}

#splash {
    margin: 0;
    width: 2400px;
    margin-left: 50%;
    transform: translateX(-1200px);
}

#centered_content {
    max-width: 1596px;
    background: $base-background;
    margin: auto;
    display: flex;
    flex-direction: column;
}

.tooltip {
    position: relative;
}

.tooltip-text {
    position: absolute;
    top: 0%;
    left: 0%;
    z-index: 1;
    opacity: 0;
    transition: opacity 1s;
    font-family: 'IBM Plex Serif', serif;
    font-style: italic;
    font-size: 15px;
    color: white;
    background-color: $brick-color;
    border-radius: 6px;
    padding: 6px 15px;
}

.tooltip:hover .tooltip-text {
    opacity: 0.8;
}
